import { http } from '@/http';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { defineComponent, onMounted, ref } from 'vue';
export default defineComponent({
    name: 'SelllerMessageDetail',
    props: {
        id: {
            type: String,
            requried: true
        }
    },
    setup(props) {
        const showMsg = ref(false);
        const title = ref('');
        const subTitle = ref('');
        const msgContent = ref('');
        const sendTime = ref('');
        const getMessageInfo = () => {
            http
                .post('/hbSeller/sellerInvite/inviteMsgDetail', { id: props.id }, false)
                .then((res) => {
                if (res.code === '200') {
                    title.value = res.data.title;
                    subTitle.value = res.data.subTitle;
                    msgContent.value = res.data.msgContent;
                    sendTime.value = res.data.ctime;
                    showMsg.value = true;
                }
                else {
                    Toast.fail(res.msg);
                }
            });
        };
        onMounted(() => {
            getMessageInfo();
        });
        return {
            showMsg,
            title,
            subTitle,
            msgContent,
            sendTime
        };
    }
});
